import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
import Json from '@/Json';
export default {
	components: {
		uniLoadMore	
	},
	data() {
		return {
			tabCurrentIndex: 0,
			allChecked: false, //全选状态  true|false
			showTabIndex:0,
			receivingGoods:false,
			
			//订单列表
			navList: [{
					state: 0,
					text: '全部',
				},
				{
					state: 1,
					text: '待付款',
				},
				{
					state: 2,
					text: '待发货',
				},{
					state: 3,
					text: '待收货',
				},
				{
					state: 4,
					text: '退款/售后',
				}
			],
			orderList:[],//订单列表
			loadingType: 'more', //more 是加载中   nomore是加载结束
			total:0,//总条数
			pageSize:0,//每页条数
			page:1,//当前页
			tempNumber:0,
			nextPage:false,//是否有下一页
			searchKey:'',//搜索的关键字
			
			
		};
	},
	
	async onLoad(options){
		
		this.tabCurrentIndex = options.state;
		if(options.tabIndex){
			this.showTabIndex = options.tabIndex;
		}
		this.togglePages(options.state);
		
	},
	onShow() { //返回显示页面状态函数
		//，来进行局部刷新
		// this.loadData();
	},
	onUnload() {   
		if(this.showTabIndex == 8){
			uni.switchTab({
				url: '../index/index'
			});
		}
		 
	},
	methods: {
		//获取订单列表
		loadData(){
			let self = this;
			self.$axios(self.$baseUrl.getOrderList,{
				page:self.page,
				params:self.searchKey
			})
			.then(res =>{
				let result = res.data.data;
				if(res.data.code == 1){
					if(self.page == 1){//第一次加载
						self.orderList = result.data;
					}else{//加载更多
				        result.data.forEach((item)=>{
							self.orderList.push(item) ;
						})
					}
					self.pageSize = result.per_page;//每页条数
					self.total = result.total;//总条数
					self.page = result.current_page; //当前页
					
					//判读是否有下一页数据
					if(self.total > self.pageSize * self.page){
						self.loadingType = 'more';
						self.nextPage = true;
					}else{
						self.loadingType = 'nomore';
						self.nextPage = false;
					}
				}
				
			},(error) =>{
				console.log(error)
			})
			if(self.loadingType === 'loading'){
				//防止重复加载
				return;
			}
			self.loadingType = 'loading';
		}, 
		//禁止手指滑动
		stopTouchMove: function() {
		  return false;
		},

		
		//顶部tab点击
		tabClick(index){
			this.tabCurrentIndex = index;
			this.orderList = [];
			this.pageSize = 0;
			this.total = 0;
			this.page = 1;
			this.togglePages(this.tabCurrentIndex);
		},
		//删除订单
		deleteOrder(index){
			uni.showLoading({
				title: '请稍后'
			})
			setTimeout(()=>{
				this.navList[this.tabCurrentIndex].orderList.splice(index, 1);
				uni.hideLoading();
			}, 600)
		},
		//取消订单
		cancelOrder(item){
			let self = this;
			let id = item.id;//订单ID
			console.log(111111,self.tabCurrentIndex)
			self.$axios(self.$baseUrl.cancel,{
				id:id
			})
			.then(res =>{
				if(res.data.code == 1){
					uni.showToast({title:'取消成功',icon:'success'});
					if(self.tabCurrentIndex == 0){
						//全部订单
						self.loadData();
					} else if(self.tabCurrentIndex == 1){
						//待付款订单
						self.unpaid();
					}
				}
			},(error) =>{
				console.log(error)
			})
		},
		//切换
		togglePages(toggleIndex){
			let self = this;
			//调接口更新数据
			if(toggleIndex == 0){
				//全部订单
				self.loadData();
			} else if(toggleIndex == 1){
				//待付款订单
				self.unpaid();
			}else if(toggleIndex == 2){
				//待发货订单
				self.unshipped();
				
			}else if(toggleIndex == 3){
				//待收货 unreceiptlist
				
				self.unReceivingGoods();
			}else if(toggleIndex == 4){
				//已完成订单
				self.afterSale();
			}
		},
		//申请退款
		refundOperation(item){
			let self = this;
			let id = item.id;//订单ID
			self.$axios(self.$baseUrl.refundMoney,{
				order_id:id
			})
			.then(res =>{
				if(res.data.code == 1){
					uni.showToast({title:'退款成功',icon:'success'});
					if(self.tabCurrentIndex == 2){
						//待发货订单
						self.unshipped();
					}else{
						//调接口更新数据
						self.loadData();
					}
					
					
				}else if(res.data.code == 0){
					
					uni.showToast({title:'申请退款正在审核中..',icon:'error'});
				}
			},(error) =>{
				console.log(error)
			})
	    },
		//确认收货
		confirmation(item){
			let self = this;
			let id = item.goods[0].order_id;//订单ID
			self.$axios(self.$baseUrl.finish,{
				id:id
			})
			.then(res =>{
				if(res.data.code == 1){
					uni.showToast({title:'确认收货成功！',icon:'success'});
					//调接口更新数据
					self.unReceivingGoods();
				}else if(res.data.code == 0){
					
				}
			},(error) =>{
				console.log(error)
			})
		},

		//触发加载更多
		loadMore(){
			let self = this;
			if(self.nextPage){
				self.page = self.page +1;
				
				
				//调接口更新数据
				self.togglePages(self.tabCurrentIndex);
			}
		},
		//待付款订单‘
		unpaid(){
			let self = this;
			self.$axios(self.$baseUrl.unpayorderlist,{
				page:self.page
			})
			.then(res =>{
				let result = res.data.data;
				if(res.data.code == 1){
					if(self.page == 1){//第一次加载
						self.orderList = result.data;
					}else{//加载更多
						result.data.forEach((item)=>{
							self.orderList.push(item) ;
						})
					}
					self.pageSize = result.per_page;//每页条数
					self.total = result.total;//总条数
					self.page = result.current_page; //当前页
					//判读是否有下一页数据
					if(self.total > self.pageSize * self.page){
						self.loadingType = 'more';
						self.nextPage = true;
					}else{
						self.loadingType = 'nomore';
						self.nextPage = false;
					}
				}
				
			},(error) =>{
				console.log(error)
			})
			
			if(self.loadingType === 'loading'){
				//防止重复加载
				return;
			}
			self.loadingType = 'loading';
		}, 
		//待发货订单‘
		unshipped(){
			let self = this;
			self.$axios(self.$baseUrl.unfreightlist,{
				page:self.page
			})
			.then(res =>{
				let result = res.data.data;
				if(res.data.code == 1){
					if(self.page == 1){//第一次加载
						self.orderList = result.data;
					}else{//加载更多
						result.data.forEach((item)=>{
							self.orderList.push(item) ;
						})
					}
					self.pageSize = result.per_page;//每页条数
					self.total = result.total;//总条数
					self.page = result.current_page; //当前页
					console.log(222222,self.orderList)
					//判读是否有下一页数据
					if(self.total > self.pageSize * self.page){
						self.loadingType = 'more';
						self.nextPage = true;
					}else{
						self.loadingType = 'nomore';
						self.nextPage = false;
					}
				}
			},(error) =>{
				console.log(error)
			})
			if(self.loadingType === 'loading'){
				//防止重复加载
				return;
			}
			self.loadingType = 'loading';
		}, 
		//待收货订单
		unReceivingGoods(){
			let self = this;
			self.$axios(self.$baseUrl.unreceiptlist,{
				page:self.page
			})
			.then(res =>{
				let result = res.data.data;
				if(res.data.code == 1){
					if(self.page == 1){//第一次加载
						self.orderList = result.data;
						self.orderList = self.orderList.map(item=>{
							item.checked = false;
							return item;
						});
					}else{//加载更多
						result.data.forEach((item)=>{
							item = item.map((todo)=>{
								todo.checked = false;
								return todo;
							})
							self.orderList.push(item) ;
						})
					}
					self.pageSize = result.per_page;//每页条数
					self.total = result.total;//总条数
					self.page = result.current_page; //当前页
					//判读是否有下一页数据
					if(self.total > self.pageSize * self.page){
						self.loadingType = 'more';
						self.nextPage = true;
					}else{
						self.loadingType = 'nomore';
						self.nextPage = false;
					}
				}
			},(error) =>{
				console.log(error)
			})
			if(self.loadingType === 'loading'){
				//防止重复加载
				return;
			}
			self.loadingType = 'loading';
		}, 
		//退款或售后（用户已完成订单(含已退款)）
		afterSale(){
			let self = this;
			self.$axios(self.$baseUrl.finishList,{
				page:self.page
			})
			.then(res =>{
				let result = res.data.data;
				if(res.data.code == 1){
					if(self.page == 1){//第一次加载
						self.orderList = result.data;
					}else{//加载更多
						result.data.forEach((item)=>{
							self.orderList.push(item) ;
						})
					}
					self.pageSize = result.per_page;//每页条数
					self.total = result.total;//总条数
					self.page = result.current_page; //当前页
					//判读是否有下一页数据
					if(self.total > self.pageSize * self.page){
						self.loadingType = 'more';
						self.nextPage = true;
					}else{
						self.loadingType = 'nomore';
						self.nextPage = false;
					}
				}
			},(error) =>{
				console.log(error)
			})
			if(self.loadingType === 'loading'){
				//防止重复加载
				return;
			}
			self.loadingType = 'loading';
		}, 
		 //选中状态处理
		check(type, index){
			let self = this;
			let tabIndex = index;
			if(type == 'item'){
				self.allChecked = 0;
				self.orderList[index].checked = !self.orderList[index].checked;
				let passed = self.orderList.every((item)=>{
					return item.checked
				});
				if(passed){
					self.allChecked = true;
				}else{
					self.allChecked = false;
				}
				if(self.orderList[index].checked){
					self.receivingGoods = true;
				}
				
			}else{
				
				self.orderList.forEach(item=>{
					item.checked = !item.checked;
				})
				self.allChecked = !self.allChecked;
				if(self.allChecked){
					self.receivingGoods = true;
				}
			}
			
		},
		//批量确认收货
		createOrder(){
			let self = this;
			let cartIdArr= [];
			let cartId = '';
			self.orderList.forEach((item,index)=>{
				if(item.checked){
					cartIdArr.push(item.goods[0].order_id);
				}
				
			})
			cartIdArr.forEach((item,index)=>{
				if(cartIdArr.length-1 > index){
					cartId += item+','
				}else{
					cartId += item;
				}
				
			})
			if(cartIdArr.length == 0){
				uni.showToast({title:'请先选择商品',icon:'none'});
			}else{
				self.$axios(self.$baseUrl.finish,{
					id:cartId
				})
				.then(res =>{
					if(res.data.code == 1){
						uni.showToast({title:'确认收货成功！',icon:'success'});
						//调接口更新数据
						self.unReceivingGoods();
					}else if(res.data.code == 0){
						
					}
				},(error) =>{
					console.log(error)
				})
		
			}
			
			
		},
		 
		//订单搜索功能
		onSearch(){
			let self = this;
			self.loadData();
		},
		
		 //支付详情
		 paymentOrder(id){
			 uni.navigateTo({
			 	url: `/pages/cart/orderdetails?id=${id}`
			 })
		},
		//查看订单详情
		orderDetails(id){
			let self = this;
			if(id ==undefined){
				uni.showToast({title:'商品已下架',icon:'none'});
			}else{
				//跳转到订单列表
				uni.navigateTo({
					url: `/pages/cart/readdetails?id=${id}`
				})
			}
			
		}
	}
}
